
.lists-wrap {
  display: block;
	width: 600px;
	margin: 0 auto;
	padding: 50px 0;
}

.lists-wrap table {
	width: 100%;
}

.title {
	font-size: 18px;
	margin-bottom: 15px;
}

.table-content {
  overflow-y: scroll;
  border-collapse: collapse;
  display: block;
}

th {
	background-color: #b7eb8f;
	color: #389e0d;
}

td {
	color: #333;
}

th, td {
	text-align: center;
	font-size: 12px;
	font-weight: bold;
	text-transform: uppercase;
	width: 100px;
}

.table-content th,
.table-content td {
	border: 1px solid #f0f0f0
}

.tr {
	width: 100%;
  height: 35px;
  max-height: 35px;
}

.tr-odd {
  background-color: #e6f7ff;
}

.tr-even {
  background-color: #fff;
}
